import axios from 'axios'

const state = {
  user: {},
  // startRec: 0,
  // totalRows: 0,
  showForm: false,
  showFormList: true,
  showFormEmployeeList: true,
  showFormEmployeeMaster: false,
  showFormLeaveBalanceList: true,
  showFormLeaveAdd: false,
  showFormLoanSummaryList: true,
  showFormLoanTransaction: false,
  showFormSalaryTransactionList: true,
  showFormSalaryTransaction: false,
  isEditMode: true,
  authenticated: true,
  showMETform: false
}
const getters = {
  user: state => state.user,
  // startRec: state => state.startRec,
  // totalRows: state => state.totalRows,
  showForm: state => state.showForm,
  showFormList: state => state.showFormList,
  isEditMode: state => state.isEditMode,
  authenticated: state => state.authenticated,
  showMETform: state => state.showMETform,
  showFormEmployeeList: state => state.showFormEmployeeList,
  showFormEmployeeMaster: state => state.showFormEmployeeMaster,
  showFormLeaveBalanceList: state => state.showFormLeaveBalanceList,
  showFormLeaveAdd: state => state.showFormLeaveAdd,
  showFormLoanSummaryList: state => state.showFormLoanSummaryList,
  showFormLoanTransaction: state => state.showFormLoanTransaction,
  showFormSalaryTransactionList: state => state.showFormSalaryTransactionList,
  showFormSalaryTransaction: state => state.showFormSalaryTransaction
}
const actions = {
  async fetchSectionList ({
    commit
  }, id) {
    const response = await axios.get(
      process.env.VUE_APP_API_URL + `/section/forDirectorate/${id}`
    )
    commit('setSectionList', response.data)
  },
  async METformDisplay ({
    commit
  }, data) {
    console.log('setMETformDisplay:' + data)
    commit('setMETformDisplay', data)
  },
  async monthEndTransactionDisplay ({
    commit
  }, data) {
    commit('setMonthEndTransactionDisplay', data)
  },
  // async fetchStartRec ({
  //   commit
  // }, no) {
  //   console.log('start rec in state:' + no)
  //   commit('setStartRec', no)
  // },
  async setFormDisplay ({
    commit
  }, x) {
    commit('setFormDisplay', x)
  },
  async setIsEditMode ({
    commit
  }, mode) {
    commit('setEditMode', mode)
  },
  async setEmployeeFormDisplay ({
    commit
  }) {
    commit('setEmployeeFormDisplay')
  },
  async setLeaveFormDisplay ({
    commit
  }) {
    commit('setLeaveFormDisplay')
  },
  async setLoanFormDisplay ({
    commit
  }) {
    commit('setLoanFormDisplay')
  }
}

const mutations = {
  setEmployeeFormDisplay (state) {
    state.showFormEmployeeList = !state.showFormEmployeeList
    state.showFormEmployeeMaster = !state.showFormEmployeeMaster
  },
  setMonthEndTransactionDisplay (state) {
    state.showFormSalaryTransaction = !state.showFormSalaryTransaction
    state.showFormSalaryTransactionList = !state.showFormSalaryTransactionList
  },
  setLeaveFormDisplay (state) {
    state.showFormLeaveBalanceList = !state.showFormLeaveBalanceList
    state.showFormLeaveAdd = !state.showFormLeaveAdd
  },
  setLoanFormDisplay (state) {
    state.showFormLoanSummaryList = !state.showFormLoanSummaryList
    state.showFormLoanTransaction = !state.showFormLoanTransaction
  },
  setFormDisplay (state, x) {
    state.showForm = x
    state.showFormList = !x
    console.log('setFormDisplay')
  },
  setEditMode: (state, mode) => (state.isEditMode = mode),
  setUser: (state, user) => (state.user = user),
  // setStartRec: (state, startRec) => (state.startRec = startRec),
  // setTotalRows: (state, totalRows) => (state.totalRows = totalRows)
  setMETformDisplay: (state, mode) => (state.showMETform = mode)
}

export default {
  state,
  getters,
  actions,
  mutations
}
