import axios from 'axios'
const getDefaultState = () => {
  return {
    employee: {
      id: 0,
      empName: '',
      deptCode: ''
    },
    employeeMET: {},
    // ot1Rate: '1',
    // ot2Rate: '2',
    // nsRate: '1',
    empLoanSummary: {},
    loanHistory: [],
    // employeeList: [],
    employeeLoan: {},
    leaveHistory: [],
    employeeLeave: {},
    metAllowance: [],
    metDeduction: [],
    message: '',
    totalRows: 0
  }
}
const getDefaultStateEmpLoan = () => {
  return {
    empLoanSummary: {},
    employeeLoan: {}
  }
}
const getDefaultStateEmpLeave = () => {
  return {
    leaveTransaction: {
      id: 0,
      empCode: 0,
      empName: '',
      dateFrom: new Date(),
      dateTo: new Date(),
      applicationDate: new Date()
    }
  }
}
const state = {
  employee: {
    id: 0,
    empName: '',
    deptCode: ''
  },
  employeeMET: {
    id: 0
  },
  rates: {},
  empLoanSummary: {},
  loanHistory: [],
  employeeList: [],
  employeeListFilter: [],
  employeeLoan: {},
  leaveHistory: [],
  employeeLeave: {},
  metAllowance: [],
  metDeduction: [],
  message: {},
  totalRows: 0,
  totalLHRows: 0,
  startRec: 0,
  activeMonth: {},
  loanHistoryList: [],
  leaveTransaction: {
    id: 0,
    empCode: 0,
    empName: '',
    dateFrom: new Date(),
    dateTo: new Date(),
    applicationDate: new Date()

  }

}

const getters = {
  employee: state => state.employee,
  employeeList: state => state.employeeList,
  employeeListFilter: state => state.employeeListFilter,
  employeeMET: state => state.employeeMET,
  employeeLoan: state => state.employeeLoan,
  empLoanSummary: state => state.empLoanSummary,
  loanHistory: state => state.loanHistory,
  employeeLeave: state => state.employeeLeave,
  leaveHistory: state => state.leaveHistory,
  rates: state => state.rates,
  // ot1Rate: state => state.ot1Rate,
  // ot2Rate: state => state.ot2Rate,
  // nsRate: state => state.nsRate,
  message: state => state.message,
  totalRows: state => state.totalRows,
  startRec: state => state.startRec,
  activeMonth: state => state.activeMonth,
  loanHistoryList: state => state.loanHistoryList,
  totalLHRows: state => state.totalLHRows,
  metAllowance: state => state.metAllowance,
  metDeduction: state => state.metDeduction,
  leaveTransaction: state => state.leaveTransaction
}

const actions = {
  defaultState ({
    commit
  }) {
    commit('resetVariables')
  },
  defaultStateEmpLoan ({
    commit
  }) {
    commit('resetEmpLoanInfo')
  },
  defaultStateEmpLeave ({
    commit
  }) {
    // console.log('in defaultStateEmpLeave')
    commit('resetEmpLeave')
  },
  async fetchStartRec ({
    commit
  }, no) {
    // console.log('start rec in state:' + no)
    commit('setStartRec', no)
  },
  async updateGosiAmount ({
    commit
  }, data) {
    // console.log('start rec in state:' + no)
    commit('setGosiAmount', data)
  },
  async updateAllowance ({
    commit
  }, data) {
    console.log(data)
    commit('setUpdateAllowance', data)
  },
  async updateEmployeeMetID ({
    commit
  }, data) {
    commit('setEmployeeMetId', data)
  },
  async fetchEmployeeList1 ({
    commit
  }, data) {
    const url = process.env.VUE_APP_API_URL + `/filterEmployee/${state.startRec}/10`
    const Response = await axios
      .post(url, data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
    commit('setEmployeeListFilter', Response.data)
    console.log('Start rec:' + state.startRec)
    console.log(state.employeeListFilter)
    commit('setTotalRows', parseInt(Response.headers.x_total_count))
  },
  async fetchRates ({
    commit
  }) {
    const url = process.env.VUE_APP_API_URL + '/rates'
    const response = await axios.get(url)
    commit('setRates', response.data)
  },
  async fetchEmployeeList ({
    commit
  }) {
    const url = process.env.VUE_APP_API_URL + '/employees'
    const response = await axios.get(url
    )
    commit('setEmployeeList', response.data)
    // commit('setTotalRows', parseInt(Response.headers.x_total_count))
    commit('setTotalRows', response.data.length)
  },
  async fetchEmployee ({
    commit
  }, id) {
    // console.log('fetchEmployee:' + id)
    const url = process.env.VUE_APP_API_URL + `/employee/${id}`
    const response = await axios.get(url)
    commit('setEmployeeData', response.data)
    // commit('setTotalRows', parseInt(Response.headers.x_total_count))
  },
  async updateEmployeeRecord ({
    commit
  }, data) {
    commit('setEmployeeData', data)
  },
  async saveEmployee ({
    commit
  }, data) {
    const url = process.env.VUE_APP_API_URL + '/employee/save'
    await axios
      .post(url, data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        // console.log('=== No Error returned ====')
        commit('setMessage', 'Sucess')
        commit('setEmployeeData', response.data)
      })
      .catch(error => {
        console.log(error.response.data)
        commit('setMessage', 'Error:' + JSON.stringify(error.response.data.errorMessage))
      })
  },
  async updateEmployee ({
    commit
  }, data) {
    // console.log('in update Employee - store')
    const url = process.env.VUE_APP_API_URL + '/employee/update'
    await axios
      .put(url, data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        // console.log('=== No Error returned ====')
        commit('setEmployeeData', response.data)
        commit('setMessage', 'Sucessfully saved!')
      })
      .catch(error => {
        // console.log(error.response.data)
        commit('setMessage', 'Error:' + JSON.stringify(error.response.data.errorMessage))
      })

    // commit('setHrRq', response.data)
  },
  async fetchemployeeMET ({
    commit
  }, id) {
    const url = process.env.VUE_APP_API_URL + `/employee/met/${id}`
    commit('setMessage', '')
    await axios
      .get(url, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        // console.log('employee met:' + JSON.stringify(response.data.otherAllowances))
        commit('setEmployeeMET', response.data)
        commit('setMetAllowance', response.data.otherAllowances)
        commit('setMetDeduction', response.data.otherDeductions)
      })
      .catch(error => {
        // console.log(error.response.data)
        commit('resetVariables')
        commit('setMessage', 'Error:' + JSON.stringify(error.response.data.errorMessage))
      })
  },
  async fetchLoanHistory ({
    commit
  }, data) {
    // console.log('fetchLoanHistory', data)
    const url = process.env.VUE_APP_API_URL + `/filterLoanTransactions/${state.startRec}/5`
    await axios
      .post(url, data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        // console.log('LoanHistory:' + JSON.stringify(response.data))
        commit('setLoanHistoryList', response.data)
        commit('setTotalLHRows', parseInt(response.headers.x_total_count))
      })
      .catch(error => {
        console.log(error.response.data)
        commit('setMessage', 'Error:' + JSON.stringify(error.response.data.errorMessage))
      })
  },
  async saveLoanTransaction ({
    commit
  }, data) {
    const url = process.env.VUE_APP_API_URL + '/employee/loan/save'
    await axios
      .post(url, data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        // console.log('=== No Error returned ====')
        commit('setMessage', 'Sucess')
        commit('setLoanHistoryList', response.data)
      })
      .catch(error => {
        console.log(error.response.data)
        commit('setMessage', 'Error:' + JSON.stringify(error.response.data.errorMessage))
      })
  },
  async fetchActiveMonth ({
    commit
  }) {
    const url = process.env.VUE_APP_API_URL + '/month/active'
    await axios
      .get(url, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        commit('setActiveMonth', response.data)
      })
      .catch(error => {
        console.log(error.response.data)
      })
  },
  async fetchEmployeeLoanInfo ({
    commit
  }, id) {
    const url = process.env.VUE_APP_API_URL + `/employee/loan/summary/${id}`
    const response = await axios.get(url)
    commit('setEmpLoanSummary', response.data)
  },
  async fetchEmployeeLoan ({
    commit
  }, id) {
    const url = process.env.VUE_APP_API_URL + `/employee/${id}`
    const response = await axios.get(url)
    commit('setEmployeeLoan', response.data)
  },
  async addRowMetAllowance ({
    commit
  }, o) {
    commit('addAllowanceRow', o)
  },
  async saveEmployeeMET ({
    commit
  }, data) {
    const url = process.env.VUE_APP_API_URL + '/employee/met/save'
    await axios
      .post(url, data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        commit('setMessage', 'Sucess')
        commit('setEmployeeMET', response.data)
      })
      .catch(error => {
        console.log(error.response.data)
        commit('setMessage', 'Error:' + JSON.stringify(error.response.data.errorMessage))
      })
  },
  async fetchLeaveHistory ({
    commit
  }, data) {
    // console.log('fetchLeaveHistory', data)
    const url = process.env.VUE_APP_API_URL + `/filterLeaveTransactions/${state.startRec}/5`
    await axios
      .post(url, data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        // console.log('LeavHistory:' + JSON.stringify(response.data))
        commit('setLeaveHistory', response.data)
        commit('setTotalLHRows', parseInt(response.headers.x_total_count))
      })
      .catch(error => {
        console.log(error.response.data)
        commit('setMessage', 'Error:' + JSON.stringify(error.response.data.errorMessage))
      })
  },
  async fetchLTEmployee ({
    commit
  }, id) {
    // console.log('fetchEmployee:' + id)
    const url = process.env.VUE_APP_API_URL + `/employee/${id}`
    const response = await axios.get(url)
    commit('setLTEmployee', response.data)
    // commit('setTotalRows', parseInt(Response.headers.x_total_count))
  },
  async saveLeaveTransaction ({
    commit
  }, data) {
    const url = process.env.VUE_APP_API_URL + '/employee/saveLeaveTransaction'
    await axios
      .post(url, data, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        commit('setMessage', 'Sucess')
        commit('setLeaveTransaction', response.data)
      })
      .catch(error => {
        console.log(error.response.data)
        commit('setMessage', 'Error:' + JSON.stringify(error.response.data.errorMessage))
      })
  }

}

const mutations = {
  resetVariables (state) {
    Object.assign(state, getDefaultState())
  },
  resetEmpLoanInfo (state) {
    Object.assign(state, getDefaultStateEmpLoan())
  },
  resetEmpLeave (state) {
    Object.assign(state, getDefaultStateEmpLeave())
  },
  setGosiAmount: (state, data) => {
    state.employee = { ...state.employee, gosiAmount: data }
  },
  setUpdateAllowance: (state, data) => {
    state.employeeMET = { ...state.employeeMET, rentAllowance: data.rentAllowanceT }
    state.employeeMET = { ...state.employeeMET, travelAllowance: data.travelAllowanceT }
  },
  setEmployeeMetId: (state, data) => {
    state.employeeMET = { ...state.employeeMET, id: data }
  },
  setRates: (state, data) => (state.rates = data),
  setTotalRows: (state, totalRows) => (state.totalRows = totalRows),
  setStartRec: (state, startRec) => (state.startRec = startRec),
  setMessage: (state, message) => (state.message = message),
  setEmployeeData: (state, employee) => (state.employee = employee),
  setEmployeeList: (state, data) => (state.employeeList = data),
  setEmployeeListFilter: (state, data) => (state.employeeListFilter = data),
  setActiveMonth: (state, data) => (state.activeMonth = data),
  setEmployeeMET: (state, data) => (state.employeeMET = data),
  setLoanHistory: (state, data) => (state.loanHistory = data),
  setLoanHistoryList: (state, data) => (state.loanHistoryList = data),
  setTotalLHRows: (state, data) => (state.totalLHRows = data),
  setEmpLoanSummary: (state, data) => (state.empLoanSummary = data),
  setEmployeeLoan: (state, data) => {
    state.employeeLoan = { ...state.employeeLoan, empCode: data.id }
    state.employeeLoan = { ...state.employeeLoan, empName: data.empName }
    state.employeeLoan = { ...state.employeeLoan, bankCode: data.bankCode }
  },
  addAllowanceRow: (state, data) => {
    state.metAllowance.push(data)
  },
  setMetAllowance: (state, data) => (state.metAllowance = data),
  setMetDeduction: (state, data) => (state.metDeduction = data),
  setLeaveHistory: (state, data) => (state.leaveHistory = data),
  setLeaveTransaction: (state, data) => (state.leaveTransaction = data),
  setLTEmployee: (state, data) => {
    state.leaveTransaction = { ...state.leaveTransaction, empCode: data.id }
    state.leaveTransaction = { ...state.leaveTransaction, empName: data.empName }
  }

  // deleteAllowanceRow: (state, pt) => {
  //   state.hrRqtempFile = []
  //   for (let i = 0, len = state.hrRq.requestDetails.length; i < len; ++i) {
  //     console.log('validate' + state.hrRq.requestDetails[i].positionTitle !== pt)
  //     if (state.hrRq.requestDetails[i].positionTitle !== pt) {
  //       const x = state.hrRq.requestDetails[i]
  //       state.hrRqtempFile.push(x)
  //     }
  //   }
  //   state.hrRq.requestDetails = state.hrRqtempFile
  //   // state.hrRq = { ...state.hrRq, hrRqDetail: state.hrRqtempFile }
  //   // console.log('hrRq.hrRqDetail->' + state.hrRq.hrRqDetail.length)
  // },
}

export default {
  state,
  getters,
  actions,
  mutations
}
